//  Travelry app style sheet
.container {
  max-width: 1170px; }
.header {
  padding: 8px 0;
  &__menu {
    &__list {
      margin-left: auto;
      color: $black; }
    &__item {
      font-size: 18px;
      margin-left: 50px;
      color: $black;
      line-height: 2; } }
  &__button {
    border: 2px solid;
    width: 116px;
    height: 46px;
    font-size: 18px;
    color: $black;
    border-radius: 5px;
    border-image: linear-gradient(45deg, rgb(255,175,189), rgb(255,195,160)) 1; } }
.navbar-light .navbar-nav .active>.nav-link {
  color: rgb(109, 82, 249); }
.navbar-light .navbar-nav .nav-link {
  color: #000000; }
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
  color: rgb(109, 82, 249); }
.banner {
  margin-top: 24px;
  padding: 90px 0;
  &__container {
    padding: 0; }
  &__contentLeft {
    padding: 40px 15px; }
  &__title {
    font-size: 120px;
    font-family: "Bebas Neue";
    color: rgb(49, 54, 86);
    line-height: 0.917; }
  &__copy {
    font-size: 22px;
    font-family: "Roboto";
    color: rgb(140, 150, 159);
    line-height: 2.273;
    text-align: left;
    padding-top: 80px; }
  &__travel {
    display: inline-flex; }
  &__Rectangle2 {
    border-radius: 10px;
    background-image: linear-gradient( 90deg, rgb(183,101,217) 0%, rgb(238,96,156) 100%);
    width: 154px;
    height: 154px; }
  &__Booking {
    padding: 50px 0; }
  &__BookTICKETSOnline {
    font-size: 30px;
    font-family: "Bebas Neue";
    color: rgb(255, 255, 255);
    line-height: 1;
    text-align: center;
    padding: 30px 40px 0; }
  &__airImg {
    width: 178px;
    height: 176px;
    margin-left: 290px;
    position: absolute;
    top: -50px; }
  &__Group1 {
    border-radius: 10px;
    background-image: linear-gradient( 90deg, rgb(76,182,191) 0%, rgb(166,234,157) 100%);
    width: 238px;
    height: 238px;
    margin-top: 30px;
    margin-left: 60px;
    left: 0; }
  &__Group1Img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25%;
    width: 50%; }
  &__buttonTrip,
  &__buttonBookTrip {
    border-radius: 5px;
    background-image: linear-gradient( -180deg, rgb(250,118,183) 0%, rgb(255,195,171) 100%);
    width: 200px;
    height: 65px;
    font-size: 20px;
    font-family: "Bebas Neue";
    color: rgb(255, 255, 255);
    text-align: center;
    text-shadow: 0px 10px 15px rgba(49, 54, 86, 0.3); } }

.values {
  background: url("/assets/img/BG.png") no-repeat left top , linear-gradient( -90deg, rgb(255,255,255) 0%, rgb(255,236,239) 100%);
  padding: 40px 0;
  background-size: cover;
  &__rectangle {
    border-radius: 30px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 30px 150px 0px rgba(49, 54, 86, 0.1);
    width: 370px;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center; }
  &__content {
    padding: 40px 35px;
    text-align: center; }
  &__expert {
    padding-top: 80px; }
  &__transportation {
    padding-top: 70px;
    margin-left: -70px; }
  &__text {
    font-size: 30px;
    font-family: "Roboto";
    color: rgb(49, 54, 86);
    line-height: 1.2;
    text-align: center;
    padding-top: 40px; }
  &__copy {
    font-size: 18px;
    font-family: "Roboto";
    color: rgb(140, 150, 159);
    line-height: 1.889;
    text-align: center;
    margin-top: 20px; }
  &__planTitle {
    display: block;
    &__Trip {
      font-size: 18px;
      font-family: "Roboto";
      color: rgb(109, 82, 249);
      text-align: left; }
    &__Title {
      font-size: 72px;
      font-family: "Bebas Neue";
      color: rgb(49, 54, 86);
      line-height: 1.528;
      text-align: left;
      padding-bottom: 50px; }
    &__Copy {
      font-size: 22px;
      font-family: "Roboto";
      font-weight: 500;
      color: rgb(140, 150, 159);
      line-height: 1.818;
      text-align: left;
      padding-right: 30px; } } }
.topdetails {
  position: relative;
  background-color: #ffffff;
  &__container {}
  &__slider {
    padding: 40px 0; }
  &__copy {
    padding: 0 0 30px; }
  &__slider {
    padding: 40px 0 20px;
    .button {
      border-width: 1px;
      border-color: rgb(217, 210, 255);
      border-style: solid;
      border-radius: 50%;
      background-color: rgb(109, 82, 249);
      box-shadow: 0px 20px 40px 10px rgba(109, 82, 249, 0.1);
      width: 48px;
      height: 48px;
      &__prev {
        margin: 15px 20px 0 0; }
      &__next {
        border-color: rgb(217, 210, 255);
        background-color: rgb(255, 255, 255);
        width: 78px;
        height: 78px; } }
    .slider {
      width: 550px;
      &__card {
        width: 100%;
        padding: 20px 30px;
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        background-color: transparent;
        background-clip: border-box;
        border: none;
        border-radius: 0; }
      &__base {
        position: relative;
        text-align: center;
        border-radius: 30px;
        background-image: linear-gradient( 0deg, rgb(246,246,255) 0%, rgb(232,249,255) 100%);
        width: 100%;
        height: 344px;
        border-width: 3px;
        border-color: rgb(255, 255, 255);
        border-style: solid;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 10px 27px 3px rgba(109, 82, 249, 0.2);
        &Img {
          width: 100%;
          height: 100%;
          border-radius: 30px; } }
      .card-body {
        margin-top: 20px; }
      .card-title {
        font-size: 30px;
        font-family: "Roboto";
        color: rgb(49, 54, 86);
        text-align: left;
        width: 400px;
        padding-top 10px {} }
      .card-text {
        font-size: 18px;
        font-family: "Roboto";
        color: rgb(140, 150, 159);
        text-align: left;
        width: 400px;
        padding: 20px 0; }
      .btn-primary {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        font-family: "Bebas Neue";
        color: rgb(255, 255, 255);
        text-shadow: 0px 10px 15px rgba(49, 54, 86, 0.3);
        border-radius: 5px;
        background-image: linear-gradient( -180deg, rgb(250,118,183) 0%, rgb(255,195,171) 100%);
        width: 200px;
        height: 65px; } } } }
.video {
  padding: 40px 0;
  background-color: rgba(#dddddd, 0.5);
  &__frame {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 890px;
    height: 557px;
    border-radius: 30px;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.2),inset 0px 1px 1px 0px rgba(255, 255, 255, 0.004);
    background-image: linear-gradient( 0deg, rgb(188,189,191) 0%, rgb(216,216,223) 10%, rgb(216,216,223) 89%, rgb(173,170,189) 100%);
    margin: 30px;
    &--iframe {
      width: 100%;
      height: 100%;
      border-radius: 30px; } } }
.owl {
  &-item {}

  &__countdown {
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 10px 100px 0px rgba(109, 82, 249, 0.08);
    width: 350px;
    height: 150px;
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    margin-right: 10px;
    &__number {
      font-size: 48px;
      font-family: "Bebas Neue";
      color: rgb(109, 82, 249);
      text-align: center; }
    &__text {
      font-size: 20px;
      font-family: "Roboto";
      color: rgb(49, 54, 86); } } }
.portfolio {
  position: relative;
  // border: 1px solid green
  &__container {
 }    // background-color: rgba(red, 0.2)
  &__Title {
    width: 1170px;
    display: block; }
  &__subTitle,
  &__topTitle {
    text-align: center;
    display: block; }
  &__topTitle {
    padding-bottom: 30px; }
  &__header {
    border: 1px solid rgba(gray, 0.5);
    height: 90px;
    border-radius: 50px;
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    background-image: linear-gradient( -180deg, rgb(246,246,255) 0%, rgb(232,249,255) 100%);
    &__list {
      position: relative;
      display: inline-flex; }
    &__item {
      display: flex;
      padding: 10px 20px;
      width: 117px;
      position: relative;
      justify-content: center;
      margin: 0 40px;
      font-size: 18px;
      font-family: "Bebas Neue";
      color: rgb(109, 82, 249);
      line-height: 6.111;
      text-align: center;
      &:not(:first-child) {
        margin-left: 0; } }
    &__link {
      border: 1px solid red;
      height: 60px;
      border-radius: 50px; }
    &__controller {
      padding: 10px 25px;
      border-radius: 10px;
      border: none;
      margin-bottom: 30px;
      font-size: 18px;
      font-family: "Bebas Neue";
      height: 60px; } } }




@media screen and (max-width: 522px) {
  .header {
    &__menu {
      background-color: rgba(green, 0.3);
      &__list {
        margin-left: 0; }
      &__item {
        margin-left: 0px; } } } }







